<template>
<div>
  <Page>
    <b-form>
        <b-form-row>
          <b-form-group label="Data Início" class="col-md-auto">
             <date-picker v-model="time1"  format="DD-MM-YYYY HH:mm:ss" class="mt-1" type="datetime"></date-picker>
          </b-form-group>
          <b-form-group label="Data Final" class="col-md-auto">
             <date-picker v-model="time2"  format="DD-MM-YYYY HH:mm:ss" class="mt-1" type="datetime"></date-picker>
          </b-form-group>
          <!-- <b-form-group label="Campanhas" class="col-md-auto">
            <b-select plain v-model="selectedQueue" :options="queues" size="md">
              <b-select-option value="">Todas as Campanhas</b-select-option>
            </b-select>
          </b-form-group> -->
          <b-form-group label="Agentes" class="col-md-auto">
            <b-select  plains multiple :options="agents" v-model="agent" />
          </b-form-group>
          <b-form-group label="Pausas" class="col-md-auto">
            <b-select plains multiple value="Select state" :options="breaks" v-model="breakValue" >
            </b-select>
          </b-form-group>
        </b-form-row>
        <b-form-row>
            <ladda-btn :loading="loading" id="showBtn" data-style="expand-left" class="btn btn-success"  @click.native="getLoginPause">Visualizar</ladda-btn>
        </b-form-row>
     </b-form>
  </Page>
  <Page :hidden="table.length == 0">
  <Table :tableData="table" :filename="filename" :header="header" :tableheader="tableHeader" :small="true"/>
  </Page>
</div>
</template>
<script>
import Page from '@/components/Page'
import DatePicker from 'vue2-datepicker'
import '@/vendor/libs/vue2-datepicker/locale/pt-br'
import moment from 'moment'
import LaddaBtn from '@/vendor/libs/ladda/Ladda'
import axios from 'axios'
import Table from '@/components/Table'
export default {
  name: 'login',
  components: {
    Page,
    DatePicker,
    LaddaBtn,
    Table
  },
  metaInfo: {
    title: 'Relatório de Login/Pausas'
  },
  data: () => ({
    time1: moment().hour(0).minute(0).second(0).toDate(),
    time2: moment().hour(23).minute(59).second(59).toDate(),
    agents: [],
    agent: [],
    breaks: [],
    breakValue: [],
    loading: false,
    table: [],
    header: [],
    legend: [],
    filename: '',
    tableHeader: []
    // queues: [],
    // selectedQueue: ''
  }),
  mounted () {
    this.$store.commit('setTitle', 'Relatório de Login/Pausas')
    this.loadAgents()
    this.loadBreaks()
    // this.loadQueues()
  },
  methods: {
    // loadQueues: function () {
    //   axios
    //     .post(
    //       `${process.env.VUE_APP_ROOT_API ??
    //         location.origin}/reportsbackend/queues.php`
    //     )
    //     .then(data => {
    //       this.queues = data.data
    //     })
    //     .catch(err => console.log(err))
    // },
    updateHeader () {
      this.header = []
      this.header.push('<h4>Relatório de Login/Pausas por Agente</h4>')
      this.header.push(`Período ${moment(this.time1).format('DD/MM/YYYY')} à ${moment(this.time2).format('DD/MM/YYYY')} / Hora à ${moment(this.time1).format('HH:mm:ss')} até ${moment(this.time2).format('HH:mm:ss')}`)
      this.header.push(' ')

      if (this.tableHeader.length > 0) {
        let customTableHeader = '<table><tr>'
        this.tableHeader.forEach(x => {
          customTableHeader += x === 'Agente' ? "<td style='background-color:#92a8d1'>" : "<td colspan='3' style='background-color:#92a8d1'>"
          customTableHeader += x
          customTableHeader += '</td>'
        })
        customTableHeader += '</tr></table>'
        this.header.push(customTableHeader)
      }

      this.filename = `Rel_Login_Pausas ${moment().format('DD_MM_YYYY_HH_mm_ss')}`

      this.legend = ['<h5>Legenda:</h5>',
        '<b>Agentes</b> - Nome ou ramal do operador;',
        '<b>Eventos</b> - Tipo de pausas;',
        '<b>Pausas</b> - Detalhes das pausas;',
        '<b>Tempo A</b> - Tempo das pausas;',
        `<b>Emitido em:</b> ${moment().format('DD/MM/YYYY HH:mm:ss')}`]
    },
    loadAgents: function () {
      axios.post(`${process.env.VUE_APP_ROOT_API ?? location.origin}/reportsbackend/agents_callcenter.php`).then(data => {
        this.agents = []
        this.agents = [
          {
            text: 'Todos',
            value: ''
          }
        ]
        data.data.forEach((x) => {
          this.agents.push({
            text: x.name,
            value: x.id
          })
        })
      }
      ).catch(err => console.log(err))
    },
    loadBreaks: function () {
      axios.post(`${process.env.VUE_APP_ROOT_API ?? location.origin}/reportsbackend/breaks.php`).then(data => {
        this.breaks = []
        this.breaks = [
          {
            text: 'Todos',
            value: ''
          }
        ]
        data.data.forEach((x) => {
          this.breaks.push({
            text: x.description,
            value: x.id
          })
        })
      }
      ).catch(err => console.log(err))
    },
    getParams: function () {
      const params = new URLSearchParams()
      params.append('time1', moment(this.time1).format('YYYY-MM-DD HH:mm:ss'))
      params.append('time2', moment(this.time2).format('YYYY-MM-DD HH:mm:ss'))
      // params.append('selectedQueue', this.selectedQueue)
      params.append('agent', this.agent.join(','))

      params.append('breaks', this.breakValue.join(','))
      return params
    },
    getLoginPause: function () {
      this.loading = true

      if (this.breakValue.includes('', 0) && this.breakValue.length > 1) {
        this.$bvToast.toast('Parametros de Pausas Inválidos', {
          title: 'Tecnomafer Report',
          autoHideDelay: 5000,
          appendToast: true,
          toastClass: 'bs4-toast bg-danger'
        })
        this.loading = false
        return
      }

      const params = this.getParams()
      axios.post(`${process.env.VUE_APP_ROOT_API ?? location.origin}/reportsbackend/report_login_detailed.php`, params).then(data => {
        if (data.data.length === 0) {
          this.$bvToast.toast('Nenhum registro encontrado', {
            title: 'Tecnomafer Report',
            autoHideDelay: 5000,
            appendToast: true,
            toastClass: 'bs4-toast bg-danger'
          })
        }
        this.mountTable(data.data)
        this.updateHeader()
      }
      ).catch(err => 'opa' + console.log(err)).finally(x => { this.loading = false })
    },
    mountTable (data) {
      // header

      this.tableHeader = []

      this.tableHeader.push('Agente')
      this.tableHeader.push('Login')
      if (this.breakValue.length === 0 || this.breakValue.includes('', 0)) {
        this.breaks.forEach(x => {
          if (x.text !== 'Todos') { this.tableHeader.push(x.text) }
        })
      } else {
        this.breakValue.forEach(x => {
          this.tableHeader.push(this.breaks.find(y => y.value === x).text)
        })
      }

      const tableTemp = []

      let item = {}

      this.agents.forEach(agent => {
        // find login
        const logins = data.filter(x => x.Pausa === 'Login' && parseInt(x.AgenteId) === parseInt(agent.value))
        if (!logins) return

        logins.forEach(login => {
          item = {
            ...item,
            Nome: login.Agente,
            Primeiro: login.Primeiro,
            Ultimo: login.Ultimo,
            Duração: moment()
              .startOf('day')
              .seconds(login.Duracao)
              .format('HH:mm:ss')
          }

          for (let i = 2; i < this.tableHeader.length; i++) {
            const findBreak = data.filter(x => String(x.Data) === String(login.Data) && x.Pausa === this.tableHeader[i] && parseInt(x.AgenteId) === parseInt(agent.value)).shift()
            item = {
              ...item,
              ['Primeiro ' + this.tableHeader[i]]: !findBreak ? '-' : findBreak.Primeiro,
              ['Último ' + this.tableHeader[i]]: !findBreak ? '-' : findBreak.Ultimo,
              ['Duração ' + this.tableHeader[i]]: !findBreak ? '-' : moment()
                .startOf('day')
                .seconds(findBreak.Duracao)
                .format('HH:mm:ss')
            }
          }
          tableTemp.push(item)
        })
      })

      this.table = tableTemp
    }
  }
}
</script>
